/**
 * Provisioning Routes
 *
 * Internal endpoints for tenant provisioning and status tracking
 */

import type { Env } from '../types';
import { verifyServiceJWT, generateServiceJWT } from '../services/jwt';

/**
 * POST /internal/provision/queue
 * Queue provisioning for a tenant (called after signup)
 * Requires service JWT authorization
 */
export async function handleProvisionQueue(
  request: Request,
  env: Env
): Promise<Response> {
  try {
    // Verify service JWT (internal only)
    const authHeader = request.headers.get('Authorization');
    if (!authHeader || !authHeader.startsWith('Bearer ')) {
      return Response.json({
        success: false,
        error: { code: 'UNAUTHORIZED', message: 'Service authorization required' },
      }, { status: 401 });
    }

    const token = authHeader.substring(7);
    const isValid = await verifyServiceJWT(env, token);

    if (!isValid) {
      return Response.json({
        success: false,
        error: { code: 'INVALID_TOKEN', message: 'Invalid service token' },
      }, { status: 401 });
    }

    // Parse request body
    const body = await request.json<{ tenantId: string }>();

    if (!body.tenantId) {
      return Response.json({
        success: false,
        error: { code: 'MISSING_TENANT_ID', message: 'Tenant ID is required' },
      }, { status: 400 });
    }

    // Get tenant info
    const tenant = await env.DB.prepare(
      `SELECT id, plan FROM tenants WHERE id = ?`
    ).bind(body.tenantId).first<{ id: string; plan: string }>();

    if (!tenant) {
      return Response.json({
        success: false,
        error: { code: 'TENANT_NOT_FOUND', message: 'Tenant not found' },
      }, { status: 404 });
    }

    // Get Provisioner Durable Object for this tenant
    const provisionerId = env.PROVISIONER.idFromName(tenant.id);
    const provisioner = env.PROVISIONER.get(provisionerId);

    // Queue provisioning
    await provisioner.fetch(new Request('https://provisioner/queue', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        tenantId: tenant.id,
        plan: tenant.plan,
      }),
    }));

    // Trigger run
    const response = await provisioner.fetch(new Request('https://provisioner/run', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        tenantId: tenant.id,
        plan: tenant.plan,
      }),
    }));

    const result = await response.json<{
      success: boolean;
      message: string;
      state?: unknown;
    }>();

    return Response.json(result);
  } catch (error) {
    console.error('[Provision Queue] Error:', error);
    return Response.json({
      success: false,
      error: {
        code: 'QUEUE_ERROR',
        message: error instanceof Error ? error.message : String(error),
      },
    }, { status: 500 });
  }
}

/**
 * GET /api/v1/tenants/:id/provision-status
 * Get provisioning status for a tenant
 * Requires owner session or admin JWT
 */
export async function handleProvisionStatus(
  request: Request,
  env: Env,
  tenantId: string
): Promise<Response> {
  try {
    // Get tenant to verify it exists and get plan
    const tenant = await env.DB.prepare(
      `SELECT id, plan, status, provisioned_at FROM tenants WHERE id = ?`
    ).bind(tenantId).first<{
      id: string;
      plan: string;
      status: string;
      provisioned_at: number | null;
    }>();

    if (!tenant) {
      return Response.json({
        success: false,
        error: { code: 'TENANT_NOT_FOUND', message: 'Tenant not found' },
      }, { status: 404 });
    }

    // Get Provisioner Durable Object for this tenant
    const provisionerId = env.PROVISIONER.idFromName(tenant.id);
    const provisioner = env.PROVISIONER.get(provisionerId);

    // Get status
    const response = await provisioner.fetch(
      new Request(`https://provisioner/status?tenantId=${tenant.id}&plan=${tenant.plan}`)
    );

    const result = await response.json<{
      success: boolean;
      state?: {
        status: string;
        currentStep: string | null;
        checkpoints: Record<string, {
          step: string;
          status: string;
          error?: string;
        }>;
        error?: string;
      };
    }>();

    if (!result.success || !result.state) {
      return Response.json({
        success: true,
        data: {
          status: tenant.provisioned_at ? 'completed' : 'pending',
          step: null,
          checkpoints: {},
        },
      });
    }

    return Response.json({
      success: true,
      data: {
        status: result.state.status,
        currentStep: result.state.currentStep,
        checkpoints: result.state.checkpoints,
        error: result.state.error,
      },
    });
  } catch (error) {
    console.error('[Provision Status] Error:', error);
    return Response.json({
      success: false,
      error: {
        code: 'STATUS_ERROR',
        message: error instanceof Error ? error.message : String(error),
      },
    }, { status: 500 });
  }
}

/**
 * GET /api/v1/tenants/:id/overview
 * Get admin overview stats for a tenant
 * Requires owner session or admin JWT
 */
export async function handleTenantOverview(
  request: Request,
  env: Env,
  tenantId: string
): Promise<Response> {
  try {
    // Get tenant info
    const tenant = await env.DB.prepare(
      `SELECT t.*, tb.primary_color, tb.secondary_color, tb.badge_url
       FROM tenants t
       LEFT JOIN tenant_brand tb ON t.id = tb.tenant_id
       WHERE t.id = ?`
    ).bind(tenantId).first<{
      id: string;
      slug: string;
      name: string;
      email: string;
      plan: string;
      status: string;
      comped: number;
      trial_ends_at: number | null;
      created_at: number;
      provisioned_at: number | null;
      primary_color: string;
      secondary_color: string;
      badge_url: string | null;
    }>();

    if (!tenant) {
      return Response.json({
        success: false,
        error: { code: 'TENANT_NOT_FOUND', message: 'Tenant not found' },
      }, { status: 404 });
    }

    // Get current month usage
    const currentMonth = new Date().toISOString().substring(0, 7); // YYYY-MM
    const usage = await env.DB.prepare(
      `SELECT action_count FROM usage_counters
       WHERE tenant_id = ? AND month = ?`
    ).bind(tenantId, currentMonth).first<{ action_count: number }>();

    const actionCount = usage?.action_count || 0;
    const limit = tenant.plan === 'starter' && tenant.comped === 0 ? 1000 : null;

    // Count fixtures
    const fixturesCount = await env.DB.prepare(
      `SELECT COUNT(*) as count FROM fixtures WHERE tenant_id = ?`
    ).bind(tenantId).first<{ count: number }>();

    // Count results
    const resultsCount = await env.DB.prepare(
      `SELECT COUNT(*) as count FROM results WHERE tenant_id = ?`
    ).bind(tenantId).first<{ count: number }>();

    // Count feed posts
    const postsCount = await env.DB.prepare(
      `SELECT COUNT(*) as count FROM feed_posts WHERE tenant_id = ?`
    ).bind(tenantId).first<{ count: number }>();

    // Get Make.com connection (if Starter)
    let makeConnection = null;
    if (tenant.plan === 'starter') {
      makeConnection = await env.DB.prepare(
        `SELECT webhook_url, scenario_id, validated_at, last_synced_at
         FROM make_connections WHERE tenant_id = ?`
      ).bind(tenantId).first<{
        webhook_url: string;
        scenario_id: string | null;
        validated_at: number | null;
        last_synced_at: number | null;
      }>();
    }

    // Get Pro automation (if Pro)
    let proAutomation = null;
    if (tenant.plan === 'pro') {
      proAutomation = await env.DB.prepare(
        `SELECT apps_script_id, apps_script_deploy_status, kv_namespace, cron_schedule, last_synced_at
         FROM pro_automation WHERE tenant_id = ?`
      ).bind(tenantId).first<{
        apps_script_id: string | null;
        apps_script_deploy_status: string | null;
        kv_namespace: string | null;
        cron_schedule: string | null;
        last_synced_at: number | null;
      }>();
    }

    return Response.json({
      success: true,
      data: {
        tenant: {
          id: tenant.id,
          slug: tenant.slug,
          name: tenant.name,
          email: tenant.email,
          plan: tenant.plan,
          status: tenant.status,
          comped: tenant.comped === 1,
          trialEndsAt: tenant.trial_ends_at,
          createdAt: tenant.created_at,
          provisionedAt: tenant.provisioned_at,
        },
        brand: {
          primaryColor: tenant.primary_color,
          secondaryColor: tenant.secondary_color,
          badgeUrl: tenant.badge_url,
        },
        usage: {
          actionCount,
          limit,
          remaining: limit ? Math.max(0, limit - actionCount) : null,
          percentUsed: limit ? Math.min(100, Math.round((actionCount / limit) * 100)) : 0,
        },
        stats: {
          fixturesCount: fixturesCount?.count || 0,
          resultsCount: resultsCount?.count || 0,
          postsCount: postsCount?.count || 0,
        },
        automation: tenant.plan === 'starter' ? makeConnection : proAutomation,
      },
    });
  } catch (error) {
    console.error('[Tenant Overview] Error:', error);
    return Response.json({
      success: false,
      error: {
        code: 'OVERVIEW_ERROR',
        message: error instanceof Error ? error.message : String(error),
      },
    }, { status: 500 });
  }
}

